.header {
    position: relative;
    background-color: #222;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 130px;
    padding: 30px 50px;
    color: white;
}

.header .logo {
    width: 300px;
}

nav {
    position: absolute;
    top: 35%;
    right: 0;
}

nav ul {
    display: block;
    table-layout: auto;
    width: 800px;
    margin: 0;
    padding: 0;
    font-family: 'Oswald', sans-serif;
    font-size: 18px;
    text-transform: uppercase;
    list-style-type: none;
}

nav ul li {
    display: inline-block;
    padding: 10px 30px;
    color: #ddd;

}

nav a, nav a:active, nav a:visited {
    text-decoration: none;
    color: #ddd;
}

nav a {
    position: relative;
}

nav a:hover {
    color: #fff;
}

nav a.active::after {
    position: absolute;
    display: block;
    content: "";
    width: 115%;
    background:#ddd;
    height: 2px;
    bottom: -3px;
    left: 0;
    margin-left: -7.5%;
}


