body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Condensed', sans-serif;
  color: #222;
}

html {
  overflow-y:scroll;
}

#root {
  min-height: 100vh;
}

a {
  text-decoration: none;
}

p a:hover {
  position: relative;
}

p a:hover::after {
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 2px;
  background: #048aab;
}



