.support img {
    max-width: 50px;
}

h2 {
    text-transform: uppercase;
}

.support {
    display: table;
    width: 700px;
    text-align: left;

    position: absolute;
    display: table;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.support .intro_text {
    text-align: left;
}


.support ul {
    display: block;
    list-style-type: none;
    margin-left: 0;
    padding-left: 0;
    margin-top: 30px;
    text-align: center;
}

.support li {
    display: inline-block;
    margin-right: 20px;
    list-style-type: none;
}

.support li a:hover {
    position: relative;
}

.support li a:hover::after {
    position: absolute;
    display: block;
    content: "";
    bottom: -8px;
    width: 100%;
    height: 2px;
    background: #048aab;
}






