.howitworks {
    margin-top: 100px;
}

.howitworks h2 {
    text-transform: uppercase;
}

.howitworks .item {
    display: block;
    margin: 0 auto;
    width: 800px;    
}

.howitworks .title {
    display: block;
    margin-left: -30px;
}

.howitworks .p {
    text-align: left;
}

.howitworks .img_workflowtypes {
    width: 900px;
}

.howitworks table {
    border-spacing: 0;
    border-collapse: collapse;
    border: 1px solid #555;
}

.howitworks table td {
    border: 1px solid #555;
    padding: 5px 8px;
}


