.footer {
    position: absolute;
    width: 100%;
    bottom: 70px;
    left: 0;
}

.footer img {
    max-width: 100px;
    bottom: 50px;
}

.footer a {
    display: inline-block;
    margin-right: 20px;
}

.footer .logos {
    position: absolute;
    top: 0;
    right: 30px;
}

.footer a:hover {
    position: relative;
}

.footer a:hover::after {
    position: absolute;
    display: block;
    content: "";
    width: 100%;
    height: 2px;
    background: #048aab;
}


